import {
  AspectRatio,
  Avatar,
  Box,
  Card,
  CardContent,
  Dropdown,
  IconButton,
  Link,
  Menu,
  MenuButton,
  MenuItem,
  Typography,
} from "@mui/joy";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import ModeEditOutlinedIcon from "@mui/icons-material/ModeEditOutlined";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import StarBorderOutlinedIcon from "@mui/icons-material/StarBorderOutlined";
import { Link as RouterLink, useParams } from "react-router-dom";
import { useGetProductsQuery } from "../../services/product";
import Pagination from "../Pagination/Pagination";
import { useState } from "react";
import Loading from "../common/Loading";
import GridList from "../common/GridList";
import { useGetEscoProductsQuery } from "../../services/esco";

function ProductItem({ product }) {
  return (
    <Card size="sm">
      <CardContent orientation="horizontal">
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <Avatar
            src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${product.esco.profilePhoto}`}
            sx={{ marginRight: 1 }}
          >
            {product.esco.name}
          </Avatar>
          <Typography
            level="body-sm"
            sx={{
              textOverflow: "ellipsis",
              width: 120,
              overflow: "hidden",
              whiteSpace: "nowrap",
            }}
          >
            {product.esco.name}
          </Typography>
        </Box>
        <Box sx={{ marginLeft: "auto" }}>
          <Dropdown>
            <MenuButton slots={{ root: IconButton }}>
              <MoreVertIcon />
            </MenuButton>
            <Menu>
              <MenuItem>
                <Typography
                  level="body-sm"
                  startDecorator={<ModeEditOutlinedIcon />}
                >
                  Edit
                </Typography>
              </MenuItem>
              <MenuItem>
                <Typography
                  level="body-sm"
                  startDecorator={<DeleteOutlinedIcon />}
                >
                  Delete
                </Typography>
              </MenuItem>
              <MenuItem>
                <Typography
                  level="body-sm"
                  startDecorator={<StarBorderOutlinedIcon />}
                >
                  Feature
                </Typography>
              </MenuItem>
            </Menu>
          </Dropdown>
        </Box>
      </CardContent>
      <AspectRatio>
        <img
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${product.coverPhoto}`}
          alt={product.name}
        />
      </AspectRatio>
      <Typography
        level="body-md"
        sx={{
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        }}
      >
        <Link
          component={RouterLink}
          to={`/products/${product.id}`}
          overlay
          underline="none"
          color="neutral"
          level="title-lg"
        >
          {product.name}
        </Link>
      </Typography>
      <Typography
        level="body-xs"
        sx={{
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        }}
      >
        {product.description}
      </Typography>
    </Card>
  );
}

export default function ProductList() {
  const [page, setPage] = useState(1);
  const { data: products, error, isFetching } = useGetProductsQuery({ page });
  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    <Typography>{error}</Typography>;
  }
  if (!!products && !!products.data.length) {
    return (
      <>
        <GridList
          items={products.data}
          renderItem={(item) => <ProductItem product={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={products.meta.currentPage}
          onSelectPage={setPage}
        ></Pagination>
      </>
    );
  }
}

export function EscoProductList() {
  const { id: escoId } = useParams();
  const [page, setPage] = useState(1);
  const {
    data: products,
    error,
    isFetching,
  } = useGetEscoProductsQuery({ escoId, page });
  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    <Typography>{error}</Typography>;
  }
  if (!!products && !!products.data.length) {
    return (
      <>
        <GridList
          items={products.data}
          renderItem={(item) => <ProductItem product={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={products.meta.currentPage}
          onSelectPage={setPage}
        ></Pagination>
      </>
    );
  }
}
