import {
  AspectRatio,
  Avatar,
  Box,
  Card,
  CardContent,
  IconButton,
  Typography,
  Link,
  MenuButton,
  Dropdown,
  MenuItem,
  Menu,
} from "@mui/joy";
import { Link as RouterLink } from "react-router-dom";
import ModeEditOutlinedIcon from "@mui/icons-material/ModeEditOutlined";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import { useGetFarmersQuery } from "../../services/farmer";
import { useState } from "react";
import Pagination from "../Pagination/Pagination";
import Loading from "../common/Loading";
import GridList from "../common/GridList";

function FarmerItem({ farmer }) {
  return (
    <Card size="sm">
      <CardContent orientation="horizontal">
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <Avatar
            src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${farmer.profilePhoto}`}
            sx={{ marginRight: 1 }}
          >
            {farmer.name}
          </Avatar>
          <Typography
            level="body-sm"
            sx={{
              width: 120,
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
              fontWeight: "bold",
            }}
          >
            <Link
              component={RouterLink}
              to={`/farmers/${farmer.id}`}
              underline="none"
              color="neutral"
              overlay
            >
              {`${farmer.firstName} ${farmer.lastName}`}
            </Link>
          </Typography>
        </Box>
        <Box sx={{ marginLeft: "auto" }}>
          <Dropdown>
            <MenuButton slots={{ root: IconButton }}>
              <MoreVertIcon />
            </MenuButton>
            <Menu>
              <MenuItem>
                <Typography
                  level="body-sm"
                  startDecorator={<ModeEditOutlinedIcon />}
                >
                  Edit
                </Typography>
              </MenuItem>
              <MenuItem>
                <Typography
                  level="body-sm"
                  startDecorator={<DeleteOutlinedIcon />}
                >
                  Delete
                </Typography>
              </MenuItem>
            </Menu>
          </Dropdown>
        </Box>
      </CardContent>
      <AspectRatio>
        <img
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${farmer.coverPhoto}`}
          alt={farmer.name}
        />
      </AspectRatio>
      <Typography
        sx={{
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        }}
        level="body-xs"
      >
        {farmer.farmDescription}
      </Typography>
    </Card>
  );
}

export default function FarmerList() {
  const [page, setPage] = useState(1);
  const { data: farmers, error, isFetching } = useGetFarmersQuery({ page });

  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    return <Typography>{error}</Typography>;
  }

  if (!!farmers && !!farmers.data.length) {
    return (
      <>
        <GridList
          items={farmers.data}
          renderItem={(item) => <FarmerItem farmer={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={farmers.meta.currentPage}
          onSelectPage={setPage}
        ></Pagination>
      </>
    );
  }
}
