import {
  Typography,
  Box,
  Sheet,
  Avatar,
  ButtonGroup,
  AspectRatio,
  FormLabel,
  FormControl,
  Stack,
  Button,
  Chip,
  ChipDelete,
} from "@mui/joy";
import { useParams } from "react-router";
import Loading from "../common/Loading";
import { useGetEscoByIdQuery } from "../../services/esco";
import VerifiedOutlinedIcon from "@mui/icons-material/VerifiedOutlined";
import SaveOutlinedIcon from "@mui/icons-material/SaveOutlined";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import KeyOutlinedIcon from "@mui/icons-material/KeyOutlined";
import Input from "../common/Input";

export default function FarmerProfile() {
  const { id: escoId } = useParams();
  const { data: esco, error, isFetching } = useGetEscoByIdQuery(escoId);

  if (!!error) {
    return <Typography>{error}</Typography>;
  }
  if (isFetching) {
    return <Loading />;
  }
  if (!!esco) {
    return (
      <Box
        sx={{
          padding: 2,
          marginBottom: 3,
          maxWidth: 800,
          marginLeft: "auto",
          marginRight: "auto",
          position: "relative",
        }}
      >
        <Sheet
          sx={{
            display: "flex",
            justifyContent: "center",
            borderRadius: "md",
          }}
          variant="soft"
          color="success"
        >
          <Box sx={{ width: 400, position: "relative" }}>
            <AspectRatio variant="plain">
              <img
                src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${esco.coverPhoto}`}
                alt={esco.name}
              />
            </AspectRatio>
            <Avatar
              size="lg"
              variant="solid"
              src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${esco.profilePhoto}`}
              sx={{
                position: "absolute",
                left: 10,
                bottom: -20,
              }}
            ></Avatar>
          </Box>
        </Sheet>
        <ButtonGroup color="warning" buttonFlex={1} sx={{ marginTop: 4 }}>
          <Button startDecorator={<DeleteOutlinedIcon />}>Delete</Button>

          <Button startDecorator={<KeyOutlinedIcon />}>Reset Password</Button>
        </ButtonGroup>

        <Box sx={{ marginTop: 4 }}>
          <FormControl sx={{ flexGrow: 1 }}>
            <FormLabel>Name</FormLabel>
            <Input value={esco.name} />
          </FormControl>
          <FormControl sx={{ flexGrow: 1, marginTop: 2 }}>
            <FormLabel>Phone number</FormLabel>
            <Input value={esco.phoneNumber} />
          </FormControl>
          <FormControl sx={{ flexGrow: 1, marginTop: 2 }}>
            <FormLabel>Website</FormLabel>
            <Input value={esco.website} />
          </FormControl>

          <FormControl sx={{ marginTop: 2 }}>
            <FormLabel>Email</FormLabel>
            <Input
              value={esco.email}
              endDecorator={
                esco.isEmailVerified ? (
                  <VerifiedOutlinedIcon color="success" />
                ) : (
                  <Button variant="soft" size="sm" color="danger">
                    Verify
                  </Button>
                )
              }
            />
          </FormControl>

          <Stack
            direction={{ xs: "column", sm: "row" }}
            spacing={2}
            sx={{ marginTop: 2 }}
          >
            <FormControl sx={{ flexGrow: 1 }}>
              <FormLabel>Latitude</FormLabel>
              <Input value={esco.latitude} />
            </FormControl>
            <FormControl
              sx={{
                flexGrow: 1,
              }}
            >
              <FormLabel>Longitude</FormLabel>
              <Input value={esco.longitude} />
            </FormControl>
          </Stack>

          <FormControl sx={{ flexGrow: 1, marginTop: 2 }}>
            <FormLabel>Farm date</FormLabel>
            <Input value={esco.incorporationDate} type="date" />
          </FormControl>
          <Box sx={{ marginTop: 2 }}>
            <FormLabel sx={{ marginBottom: 1 }}>specialization</FormLabel>
            {esco.specialization.split(",").map((specialization) => (
              <Chip
                color="success"
                sx={{ marginRight: 2 }}
                endDecorator={<ChipDelete />}
              >
                {specialization}
              </Chip>
            ))}
          </Box>
        </Box>
        <Button
          size="md"
          color="success"
          variant="soft"
          startDecorator={<SaveOutlinedIcon />}
          sx={{
            marginTop: 3,
            width: "100%",
            position: "sticky",
            bottom: 0,
            zIndex: "tooltip",
          }}
        >
          Save
        </Button>
      </Box>
    );
  }
}
