import {
  AspectRatio,
  Avatar,
  Box,
  Card,
  CardContent,
  IconButton,
  Link,
  Typography,
  Tooltip,
} from "@mui/joy";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import { Link as RouterLink, useParams } from "react-router-dom";
import { useGetFarmerFavoriteProductsQuery } from "../../services/farmer";
import { useState } from "react";
import Pagination from "../Pagination/Pagination";
import Loading from "../common/Loading";
import Empty from "../common/Empty";
import GridList from "../common/GridList";

function InterestItem({ product }) {
  return (
    <Card size="sm">
      <CardContent orientation="horizontal">
        <Box sx={{ display: "flex", alignItems: "center" }}>
          <Avatar
            src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${product.esco.profilePhoto}`}
            sx={{ marginRight: 1 }}
          >
            {product.esco.name}
          </Avatar>
          <Typography level="body-sm">{product.esco.name}</Typography>
        </Box>
        <Box sx={{ marginLeft: "auto" }}>
          <Tooltip arrow title="Remove from interests" size="sm">
            <IconButton>
              <DeleteOutlinedIcon />
            </IconButton>
          </Tooltip>
        </Box>
      </CardContent>
      <AspectRatio>
        <img
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${product.coverPhoto}`}
          alt={product.name}
        />
      </AspectRatio>
      <Typography
        level="body-md"
        sx={{
          width: 1,
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
          fontWeight: "bold",
        }}
      >
        <Link
          component={RouterLink}
          to={`/products/${product.id}`}
          overlay
          underline="none"
          color="neutral"
          level="title-lg"
        >
          {product.name}
        </Link>
      </Typography>
      <Typography
        level="body-xs"
        sx={{
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        }}
      >
        {product.description}
      </Typography>
    </Card>
  );
}
export default function InterestList() {
  const { id: farmerId } = useParams();
  const [page, setPage] = useState();
  const {
    data: interests,
    error,
    isFetching,
  } = useGetFarmerFavoriteProductsQuery({ page, farmerId });
  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    <Typography>{error}</Typography>;
  }
  if (!!interests && !!interests.data.length) {
    return (
      <Box sx={{ padding: 3 }}>
        <GridList
          items={interests.data}
          renderItem={(item) => <InterestItem intereset={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={interests.meta.currentPage}
          onSelectPage={setPage}
        ></Pagination>
      </Box>
    );
  } else {
    return <Empty>No interests found</Empty>;
  }
}
