import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const farmerApi = createApi({
  reducerPath: "farmerApi",
  baseQuery: fetchBaseQuery({ baseUrl: process.env.REACT_APP_API_BASE_URL }),
  endpoints: (builder) => ({
    getFarmers: builder.query({
      query: ({ page }) => `farmers?page=${page}`,
    }),
    getFarmerById: builder.query({
      query: (farmerId) => `farmers/${farmerId}`,
    }),

    getFarmerFavoriteProducts: builder.query({
      query: ({ farmerId, page }) =>
        `farmers/${farmerId}/products/favorites?page=${page}`,
    }),
    getFarmerOffers: builder.query({
      query: ({ farmerId, page }) => `farmers/${farmerId}/offers?page=${page}`,
    }),
    getFarmerInstallations: builder.query({
      query: ({ farmerId, page }) =>
        `farmers/${farmerId}/installations?page=${page}`,
    }),
  }),
});

export const {
  useGetFarmersQuery,
  useGetFarmerByIdQuery,
  useGetFarmerFavoriteProductsQuery,
  useGetFarmerOffersQuery,
  useGetFarmerInstallationsQuery,
} = farmerApi;
