import {
  Card,
  CardContent,
  Avatar,
  Typography,
  AspectRatio,
  Link,
  Stack,
  IconButton,
} from "@mui/joy";
import { Link as RouterLink, useParams } from "react-router-dom";
import AgricultureOutlinedIcon from "@mui/icons-material/AgricultureOutlined";
import { useState } from "react";
import Loading from "../common/Loading";
import { useGetFarmerInstallationsQuery } from "../../services/farmer";
import { useGetEscoInstallationsQuery } from "../../services/esco";

import Pagination from "../Pagination/Pagination";
import StarRating from "../common/StarRating";
import GridList from "../common/GridList";

function InstallationItem({ installation }) {
  return (
    <Card size="sm">
      <CardContent orientation="horizontal">
        <Avatar
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${installation.esco.profilePhoto}`}
          sx={{ marginRight: 1 }}
        >
          {installation.esco.name}
        </Avatar>
        <Typography
          level="body-sm"
          sx={{
            display: "block",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            fontWeight: "bold",
            width: 1,
            alignSelf: "center",
          }}
        >
          {installation.esco.name}
        </Typography>
      </CardContent>
      <AspectRatio>
        <img
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${installation.product.coverPhoto}`}
          alt={installation.name}
        />
      </AspectRatio>
      <Stack direction="row">
        <IconButton>
          <AgricultureOutlinedIcon />
        </IconButton>
        <Typography
          level="body-sm"
          sx={{ alignSelf: "center" }}
        >{`${installation.farmer.firstName} ${installation.farmer.lastName}`}</Typography>
      </Stack>
      <Link
        component={RouterLink}
        to={`/installations/${installation.id}`}
        overlay
        underline="none"
        color="neutral"
        level="body-sm"
        sx={{
          display: "block",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
          fontWeight: "bold",
          width: 1,
        }}
      >
        {installation.product.name}
      </Link>
      <StarRating value={3} />
    </Card>
  );
}

export default function FarmerInstallationList() {
  const { id: farmerId } = useParams();

  const [page, setPage] = useState(1);
  const {
    data: installations,
    error,
    isFetching,
  } = useGetFarmerInstallationsQuery({ farmerId, page });
  return (
    <InstallationList
      installations={installations}
      onSelectPage={setPage}
      error={error}
      isFetching={isFetching}
    />
  );
}

export function EscoInstallationList() {
  const { id: escoId } = useParams();
  const [page, setPage] = useState(1);
  const {
    data: installations,
    error,
    isFetching,
  } = useGetEscoInstallationsQuery({ escoId, page });

  return (
    <InstallationList
      installations={installations}
      onSelectPage={setPage}
      error={error}
      isFetching={isFetching}
    />
  );
}

function InstallationList({ installations, onSelectPage, isFetching, error }) {
  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    return <Typography>{error}</Typography>;
  }
  if (!!installations && !!installations.data.length) {
    return (
      <>
        <GridList
          items={installations.data}
          renderItem={(item) => <InstallationItem installation={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={installations.meta.currentPage}
          onSelectPage={onSelectPage}
        ></Pagination>
      </>
    );
  }
}
