import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";

export const escoApi = createApi({
  reducerPath: "escoApi",
  baseQuery: fetchBaseQuery({ baseUrl: process.env.REACT_APP_API_BASE_URL }),
  endpoints: (builder) => ({
    getEscos: builder.query({
      query: ({ page }) => `escos?page=${page}`,
    }),
    getEscoById: builder.query({
      query: (escoId) => `escos/${escoId}`,
    }),

    getEscoProducts: builder.query({
      query: ({ escoId, page }) => `escos/${escoId}/products?page=${page}`,
    }),
    getEscoOffers: builder.query({
      query: ({ escoId, page }) => `escos/${escoId}/offers?page=${page}`,
    }),
    getEscoInstallations: builder.query({
      query: ({ escoId, page }) => `escos/${escoId}/installations?page=${page}`,
    }),
  }),
});

export const {
  useGetEscosQuery,
  useGetEscoByIdQuery,
  useGetEscoProductsQuery,
  useGetEscoOffersQuery,
  useGetEscoInstallationsQuery,
} = escoApi;
