import {
  AspectRatio,
  Avatar,
  Box,
  Card,
  CardContent,
  IconButton,
  Typography,
  MenuItem,
  Menu,
  Dropdown,
  MenuButton,
  Link,
} from "@mui/joy";
import { Link as RouterLink } from "react-router-dom";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import ModeEditOutlinedIcon from "@mui/icons-material/ModeEditOutlined";
import DeleteOutlinedIcon from "@mui/icons-material/DeleteOutlined";
import { useGetEscosQuery } from "../../services/esco";
import Pagination from "../Pagination/Pagination";
import { useState } from "react";
import Loading from "../common/Loading";
import GridList from "../common/GridList";

function EscoItem({ esco }) {
  return (
    <Card size="sm">
      <CardContent orientation="horizontal">
        <Box>
          <Avatar
            src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${esco.profilePhoto}`}
            sx={{ marginRight: 1, flexGrow: 1 }}
          >
            {esco.name}
          </Avatar>
        </Box>
        <Link
          component={RouterLink}
          to={`/escos/${esco.id}`}
          overlay
          underline="none"
          color="neutral"
          sx={{
            display: "block",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
            fontWeight: "bold",
            alignSelf: "center",
            marginRight: "auto",
            maxWidth: 1,
          }}
          level="body-sm"
        >
          {esco.name}
        </Link>
        <Dropdown>
          <MenuButton slots={{ root: IconButton }}>
            <MoreVertIcon />
          </MenuButton>
          <Menu>
            <MenuItem>
              <Typography
                level="body-sm"
                startDecorator={<ModeEditOutlinedIcon />}
              >
                Edit
              </Typography>
            </MenuItem>
            <MenuItem>
              <Typography
                level="body-sm"
                startDecorator={<DeleteOutlinedIcon />}
              >
                Delete
              </Typography>
            </MenuItem>
          </Menu>
        </Dropdown>
      </CardContent>
      <AspectRatio>
        <img
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${esco.coverPhoto}`}
          alt={esco.name}
        />
      </AspectRatio>
    </Card>
  );
}

export default function EscoList() {
  const [page, setPage] = useState(1);
  const { data: escos, error, isFetching } = useGetEscosQuery({ page });

  if (isFetching) {
    return <Loading />;
  }

  if (!!error) {
    <Typography>{error}</Typography>;
  }

  if (!!escos && !!escos.data.length) {
    return (
      <>
        <GridList
          items={escos.data}
          renderItem={(item) => <EscoItem esco={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={escos.meta.currentPage}
          onSelectPage={setPage}
        ></Pagination>
      </>
    );
  }
}
