import {
  Card,
  Typography,
  CardContent,
  Avatar,
  AspectRatio,
  Chip,
  Box,
  IconButton,
} from "@mui/joy";
import ChevronRightOutlinedIcon from "@mui/icons-material/ChevronRightOutlined";
import { Link as RouterLink } from "react-router-dom";
import GridList from "../common/GridList";
import { useGetFarmerOffersQuery } from "../../services/farmer";
import { useGetEscoOffersQuery } from "../../services/esco";
import { useParams } from "react-router";
import { toast } from "react-toastify";
import Loading from "../common/Loading";
import Empty from "../common/Empty";
import { useState } from "react";
import Pagination from "../Pagination/Pagination";

function OfferItem({
  offer: { product, esco, farmer, isAccepted, expiryDate },
}) {
  const getOfferStatus = () => {
    const isPending = isAccepted === null;
    const isRejected = isAccepted;
    const isExpired =
      (isPending || isRejected) && Date.parse(expiryDate) < Date.now();
    return isExpired
      ? "Expired"
      : isPending
      ? "Pending"
      : isRejected
      ? "Rejected"
      : isAccepted
      ? "Accepted"
      : "Unknown";
  };
  return (
    <Card size="sm">
      <Card size="sm">
        <CardContent orientation="horizontal">
          <Avatar
            size="sm"
            src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${esco.profilePhoto}`}
          ></Avatar>
          <Typography level="body-sm" sx={{ alignSelf: "center" }}>
            {esco.name}
          </Typography>
          <IconButton
            sx={{ marginLeft: "auto" }}
            component={RouterLink}
            to={`/escos/${esco.id}`}
          >
            <ChevronRightOutlinedIcon />
          </IconButton>
        </CardContent>
      </Card>
      <AspectRatio>
        <img
          src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${product.coverPhoto}`}
          alt={product.name}
        />
      </AspectRatio>
      <Typography>{product.name}</Typography>
      <Card size="sm">
        <CardContent orientation="horizontal">
          <Avatar
            size="sm"
            src={`${process.env.REACT_APP_MEDIA_BASE_URL}/${farmer.profilePhoto}`}
          ></Avatar>
          <Typography
            level="body-sm"
            sx={{ alignSelf: "center" }}
          >{`${farmer.firstName} ${farmer.lastName}`}</Typography>
          <IconButton
            sx={{ marginLeft: "auto" }}
            component={RouterLink}
            to={`/farmers/${farmer.id}`}
          >
            <ChevronRightOutlinedIcon />
          </IconButton>
        </CardContent>
      </Card>
      <Box sx={{ display: "flex" }}>
        <Chip color="warning" sx={{ marginLeft: "auto" }}>
          {getOfferStatus()}
        </Chip>
      </Box>
    </Card>
  );
}

export function FarmerOfferList() {
  const { id: farmerId } = useParams();
  const [page, setPage] = useState(1);
  const {
    data: offers,
    isFetching,
    error,
  } = useGetFarmerOffersQuery({ farmerId, page });
  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    return toast.error(error?.message);
  }
  if (!!offers) {
    return <OfferList offers={offers} setPage={setPage} />;
  }
}

export function EscoOfferList() {
  const { id: escoId } = useParams();
  const [page, setPage] = useState(1);
  const {
    data: offers,
    isFetching,
    error,
  } = useGetEscoOffersQuery({ escoId, page });
  if (isFetching) {
    return <Loading />;
  }
  if (!!error) {
    return toast.error(error?.message);
  }
  if (!!offers) {
    return <OfferList offers={offers} setPage={setPage} />;
  }
}

function OfferList({ offers, setPage }) {
  console.log(offers);
  if (!!offers && !!offers.data.length) {
    return (
      <>
        <GridList
          items={offers.data}
          renderItem={(item) => <OfferItem offer={item} />}
        />
        <Pagination
          pageCount={10}
          currentPage={offers.meta.currentPage}
          onSelectPage={setPage}
        ></Pagination>
      </>
    );
  } else {
    return <Empty>No offers found</Empty>;
  }
}
